ol{
    list-style-type: disc;
}
.body
{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: x-large;
    margin-Left: 15%;
    margin-Top: 10px;
    padding: 12px 16px;
    height: 100px;
}

hr{
    border: none;
    border-top: 2px solid black;
    margin: 10px 0;
}

.main_heading{
    text-align: center;
}
.img-wrapper {
    text-align: center;
}
table
{
    width:100%
}
table,td,th{
    border: 4px solid #111;
    border-collapse: collapse;
}
th, td {
    padding: 15px;
    text-align: center;
}

th , td h3
{
    text-align: left;
    text-align: underline;
}
.table-header{
    background-color: coral;
}
td text{
    color: #F8CF50;
}
.center-para, .centre
{
    text-align: center;
}
.r-center-para
{
    text-align: center;
    color:red;
}
.complement
{
    text-decoration: overline;
}
input{
    width: 20%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  display:inline-block;
  font-size: bolder;
}
input:focus {
    background-color: lightblue;
  }
button
{
    font-size: 25px;
    border-radius: 8px;
    transition-duration: 0.4s;
}
button:hover {
    background-color: #4CAF50; 
    color: white;
  }
  .try_span {
    font-weight: bold;
  }
.bold
{
    font-size: x-large;
    font-weight: bold;
}
.r-center-parap
{
    text-align: center;
    color:red;
    text-decoration: underline;
}
